@import "../../styles/vars.scss";

.watchlistLength {
    padding: 0 7px;
    margin-left: 7px;
    border-radius: 10px;
    color: $bg-color-main;
    background-color: $decoration-color-main;
}


@media screen and (max-width: 1280px) {
    .watchlist {
        grid-template-columns: repeat(5, 1fr);
    }
}